.layout {
    display: flex;
    flex-direction: column;
    height: 100vh;
}

/* –û–±–ª–∞—Å—Ç—å —Ö–µ–¥–µ—Ä–∞ + —Å–∞–π–¥–±–∞—Ä–∞ + –∫–æ–Ω—Ç–µ–Ω—Ç—É */
.layout-body {
    display: flex;
    flex: 1;
    transition: all 0.3s ease;
    padding-top: 0;
}

/* –ö–æ–Ω—Ç–µ–Ω—Ç */
.layout-content {
    flex: 1;
    padding: 20px;
    background: #fff;
    transition: all 0.3s ease;
    min-height: 100vh;
}

/* –Ø–∫—â–æ –º–µ–Ω—é –∑–∞–∫—Ä–∏—Ç–µ ‚Äì –≤—ñ–¥—Å—Ç—É–ø –º—ñ–Ω—ñ–º–∞–ª—å–Ω–∏–π (60px) */
.layout-body.collapsed-sidebar .layout-content {
    margin-left: 60px;
}

/* –Ø–∫—â–æ –º–µ–Ω—é –≤—ñ–¥–∫—Ä–∏—Ç–µ ‚Äì –∫–æ–Ω—Ç–µ–Ω—Ç –∑–≤—É–∂—É—î—Ç—å—Å—è (220px) */
.layout-body.with-sidebar .layout-content {
    margin-left: 220px;
}